
  /*--------------------------------------------------------------
  # About
  --------------------------------------------------------------*/
 
 .about{
      
  }
  @media (min-width: 1199px) {
    .about{
        padding-left: 28%;
        
    }
  }
  @media (min-width: 1300px) {
    .about{
        padding-left: 22%;
        
    }
  }
  
  .about .content h3 {
    font-weight: 700;
    font-size: 26px;
    color: #173b6c;
  }
  
  .about .content ul {
    list-style: none;
    padding: 0;
  }
  
  .about .content ul li {
    margin-bottom: 20px;
    display: flex;
    align-items: center;
  }
  
  .about .content ul strong {
    margin-right: 10px;
  }
  
  .about .content ul i {
    font-size: 16px;
    margin-right: 5px;
    color: #149ddd;
    line-height: 0;
  }
  
  .about .content p:last-child {
    margin-bottom: 0;
  }
  